  cursor: pointer;
  box-shadow: 0 6px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}
#faPanel[data*="technologies"] .custom-fa-panel .slide-buttons-container .selected-panel-button{
