
#customMenu {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  top: 0;
  right: 0;
  z-index: 999;
}
#customMenu ul {
  list-style: none;
  padding-right: 30px;
}
#customMenu ul li {
  width: 70px;
  height: 50px;
}

/* HOME LINKS STYLES */
#homeComponent #customMenu ul li .menu-link{
  height: 50px;
  color: #00FFA3;
  font-size: 20px;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}
#homeComponent #customMenu ul li .menu-link:hover {
  height: 100px;
  color: white;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  text-decoration: none;
  border-bottom: solid 1px white;
  padding-right: 6px;
}
#homeComponent #customMenu ul li .current-page {
  height: 100px;
  color: white !important;
  border-bottom: solid 1px white;
  padding-right: 6px;
}
#homeComponent #customMenu ul li .menu-link:focus {
  color: white;
  text-decoration: none;
}

/* ABOUT LINKS STYLES */
#aboutComponent #customMenu ul li .menu-link{
  height: 50px;
  color: #442CAC;
  font-size: 20px;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}
#aboutComponent #customMenu ul li .menu-link:hover{
  height: 100px;
  color: #442CAC !important;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  text-decoration: none;
  border-bottom: solid 1px #442CAC;
  padding-right: 6px;
}
#aboutComponent #customMenu ul li .menu-link:focus {
  color: #442CAC;
  text-decoration: none;
}
#aboutComponent #customMenu ul li .current-page{
  color: #442CAC !important;
  border-bottom: solid 1px #442CAC;
  padding-right: 6px;
}

