
#socialLinks {
  width: auto;
  height: 50px;
  position: fixed;
  bottom: 0;
  z-index: 998;
}
#socialLinks ul {
  list-style: none;
  padding: 0;
}
#socialLinks ul li {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: #FFF;
  border-radius: 100%;
  margin: 0 20px;
}
#socialLinks ul li a {
  width: inherit;
  height: inherit;
  display: inline-block;
}
#socialLinks ul li a .social-link-icon {
  font-size: 1.5em;
  color: #442CAC;
  width: inherit;
  height: inherit;
  display: inline-block;
  text-align: center;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}
#socialLinks ul li a:hover .social-link-icon {
  font-size: 1.5em;
  color: #442CAC;
  width: inherit;
  height: inherit;
  display: inline-block;
  text-align: center;
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}




